import { RouterModule, Routes } from '@angular/router';
import { CommonModule, DatePipe } from '@angular/common';
import { NgModule } from '@angular/core';
import { MatToolbarModule } from '@angular/material/toolbar'
import { MatTableModule } from '@angular/material/table';
import { MatButtonModule } from '@angular/material/button';
import { MatSelectModule } from '@angular/material/select';
import { MatFormFieldModule } from '@angular/material/form-field';
import { ReactiveFormsModule } from '@angular/forms';
import { MatInputModule } from '@angular/material/input';
import { FuseAlertComponent } from '@fuse/components/alert';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { MatIconModule } from '@angular/material/icon';
import { AppAlertService } from '@shared/service/app/alert.service';
import { MatTooltipModule } from '@angular/material/tooltip';
import { MatAutocompleteModule } from '@angular/material/autocomplete';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatSortModule } from '@angular/material/sort';
import { TablaDinamicaModule } from '@shared/components/tabla-dinamica/tabla-dinamica.module';
import { MatDialogModule } from '@angular/material/dialog';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { VentaGenericasService } from './service/venta-generica.service';
import { VentaGenericasComponent } from './components/venta-genericas.component';
import { AltaEdicionVentaComponent } from './components/alta-edicion/alta-edicion.component';
import { CalcularUnitarioComponent } from './components/calcular-unitario/calcular-unitario.component';
import { ClientesService } from 'app/modules/administracion/clientes/service/clientes.service';
import { SharedComponentModule } from '@shared/components/shared-component.module';
import { FilterMenuComponent } from './components/filter-menu/filter-menu.component';
const routes: Routes = [
    {
        path: '',
        component: VentaGenericasComponent
    },
    {
        path: 'alta',
        component: AltaEdicionVentaComponent
    },
    {
        path: 'edicion/:idventa',
        component: AltaEdicionVentaComponent
    }
];
@NgModule({
    declarations: [VentaGenericasComponent, AltaEdicionVentaComponent, FilterMenuComponent, CalcularUnitarioComponent],
    imports: [
        CommonModule,
        RouterModule.forChild(routes),
        MatTableModule,
        MatButtonModule,
        MatSelectModule,
        MatFormFieldModule,
        ReactiveFormsModule,
        MatInputModule,
        MatIconModule,
        FuseAlertComponent,
        MatTooltipModule,
        MatAutocompleteModule,
        MatToolbarModule,
        MatCheckboxModule,
        MatSortModule,
        TablaDinamicaModule,
        MatDialogModule,
        MatDatepickerModule,
        SharedComponentModule
    ],
    exports: [RouterModule],
    providers: [VentaGenericasService, JedsMenuService, AppAlertService, DatePipe, ClientesService]
})
export class VentaGenericasModule { }