import { Component, Inject, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { fuseAnimations } from '@fuse/animations';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';
import { IProveedor } from 'app/modules/administracion/proveedores/models/proveedor';
import { firstValueFrom, map, Observable, startWith, Subject, takeUntil } from 'rxjs';
import { VentaGenericasService } from '../../service/venta-generica.service';
import { ISelectResponse } from '@shared/models/select-response';
import { IUsuario } from '@core/user/user.types';
import { ICliente } from 'app/modules/administracion/clientes/models/cliente';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-filter-menu-ventas',
  templateUrl: './filter-menu.component.html',
  styleUrls: ['./filter-menu.component.scss'],
  animations: fuseAnimations
})
export class FilterMenuComponent implements OnInit {
  public formFilters: FormGroup;
  public proveedores: IProveedor[] = [];
  color_primario: string;
  color_secundario: string;
  default_color: string = 'bg-primary';
  private _unsubscribeAll: Subject<any> = new Subject<any>();

  clientes: ICliente[];
  filteredClientes: Observable<ISelectResponse[]>;
  vendedores: IUsuario[];
  filteredVendedores: Observable<IUsuario[]>;


  constructor(
    @Inject('MENU_DATA') public data: any,
    private _empresaService: EmpresaService,
    private menuService: JedsMenuService,
    private datePipe: DatePipe,
    private _ventaGenericaService: VentaGenericasService
  ) {
    // Subscribe to empresa data
    this._empresaService.empresa$
      .pipe(takeUntil(this._unsubscribeAll))
      .subscribe(empresa => {
        this.color_primario = empresa.color_primario;
        this.color_secundario = empresa.color_secundario;
      });
  }

  ngOnInit(): void {
    this.formFilters = new FormGroup({
      filtroFechaInicio: new FormControl(('')),
      filtroFechaFin: new FormControl(('')),
      cliente: new FormControl(('')),
      vendedor: new FormControl((''))
    });

    // Load empresa data
    this._empresaService.getEmpresa();
    this.formFilters.patchValue(this.data.filter);
    this.getClientes();
    this.getVendedores();

  }

  resetFilters() {
    this.formFilters.reset({
      filtroFechaInicio: '',
      filtroFechaFin: '',
      cliente: -1,
      vendedor: -1,
    });
  }

  onCloseMenu(data: any = null) {
    if (data) {
      data.filtroFechaInicio = this.datePipe.transform(this.formFilters.get('filtroFechaInicio').value, 'yyyy-MM-dd');
      data.filtroFechaFin = this.datePipe.transform(this.formFilters.get('filtroFechaFin').value, 'yyyy-MM-dd');
    }
    this.menuService.close(data);
  }

  async getClientes(option?) {
    const Clientes = await firstValueFrom(this._ventaGenericaService.getClientes(option));
    new Promise(async (resolve) => {
      this.clientes = Clientes.list;
      this.filteredClientes = this.formFilters.get('cliente').valueChanges.pipe(
        startWith(''),
        map((value) => this._filterSelect(value, 'clientes')),
      );

      resolve(true);
    })
  };

  async getVendedores(option?) {
    const Vendedores = await firstValueFrom(this._ventaGenericaService.getVendedores(option));
    new Promise(async (resolve) => {
      this.vendedores = Vendedores['list'];
      this.filteredVendedores = this.formFilters.get('vendedor').valueChanges.pipe(
        startWith(''),
        map((value) => this._filterVendodres(value)),
      );

      resolve(true);
    })
  };

  getTextGenerico(option) {
    if (option.RazonSocial || option.razonSocial) return option ? option.RazonSocial || option.razonSocial : '';

    if (option.descripcion) return option ? option.descripcion : '';

    if (option.nick) return option ? `${option.nombre} ${option.apellido}` : '';
  }

  /**
   * 
   * @param item Valor a buscar
   * @param model Modelo donde se retorna la lista de valores encontrados
   * @returns Valores encontrados en la lista
   */
  private _filterSelect(item: ISelectResponse, model?: string): ISelectResponse[] {
    const filterValue = item.toString().toLowerCase();
    return this[`${model}`].filter(item => item.descripcion.toLowerCase().includes(filterValue));
  }

  private _filterVendodres(vendedor: IUsuario): IUsuario[] {
    const filterValue = vendedor.toString().toLowerCase();
    return this.vendedores.filter(vendedor => vendedor.nombre.toLowerCase().includes(filterValue));
  }

  /**
* Funcion para limpiar campos
*/
  limpiarCasillero(input) {

    if (input === 'fecha') {
      this.formFilters.get('filtroFechaInicio').setValue('');
      this.formFilters.get('filtroFechaFin').setValue('');
    }

    if (this.formFilters.get(input)) {
      this.formFilters.get(input).setValue(-1);
    }
  }
}