import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { AuthService } from '@core/auth/auth.service';
import { dtoCalcularItem, dtoCalcularUnitario, dtoCrearVenta, dtoEditarVenta, IVenta, IVentaGenericaResponse } from '../models/venta-generica';
import { environment } from 'environments/environment';
import { map, Subject } from 'rxjs';
import { IResponse } from '@shared/models/response-with-token';

@Injectable()
export class VentaGenericasService {
    constructor(private http: HttpClient, private _authService: AuthService) { }

    /**
     * 
     * @param ordenes_de_servicio
     * @returns 
     */
    reporteOrdenServicio(ordenes_de_servicio: []) {
        return this.http.post<IVentaGenericaResponse>(`${environment.reporte_orden_servicio}`, ordenes_de_servicio);
    }

    saveVenta(payload: dtoCrearVenta) {
        return this.http.post<IResponse<IVenta>>(`${environment.ventas.vta_ventas}`, payload);
    }

    updateVenta(idventa: number, payload: dtoEditarVenta) {
        return this.http.put<IVentaGenericaResponse>(`${environment.ventas.vta_ventas}` + '/' + idventa, payload);
    }

    deleteVenta(idventa) {
        return this.http.delete<IVentaGenericaResponse>(`${environment.ventas.vta_ventas}` + '/' + idventa);
    }

    getDatosVenta(idventa: number) {
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.ventas.vta_venta_get_datos}` + '?idventa=' + idventa)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getClientes(option?) {
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.administracion.select_clientes}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getVendedores(option?) {
        let f = '?sort=RazonSocial&filter[vendedor]=1';
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.login.view_usuarios}${f}`)
            .subscribe((resp: any) => {
                this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable();
    }

    getArticulos(idarticulo?: number, filterLike?: string) {
        let f = `?filter[activo]=1&pagination=1&per-page=10&page=1`;
        if (idarticulo) {
            f += `&filter[idarticulo]=${idarticulo}`;
        } else if (filterLike) {
            f += `&filter[or][0][codigo_interno][like]=${filterLike}&filter[or][1][descripcion][like]=${filterLike}`;
        }

        let subject = new Subject<any>();
        this.http.get<any>(`${environment.stock.articulos}${f}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    getUnidadesDeMedidaArticulos(idarticulo) {
        let f = `?filter[idarticulo]=${idarticulo}`
        f += `&expand=um`
        let subject = new Subject<any>();
        this.http.get<Response[]>(`${environment.stock.articulo_unidad_medidas}${f}`)
            .subscribe((resp: any) => {
                // this._authService.accessToken = resp.token;
                subject.next({
                    list: resp.result
                })
            });
        return subject.asObservable()
    }

    getLocalidades() {
        let subject = new Subject<any>();
        this.http.get<any>(`${environment.localidades}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    getTratamiendos() {
        let subject = new Subject<any>();
        this.http.get<any>(`${environment.administracion.select_tratamientos}`).pipe(
            map((resp => resp))
        ).subscribe((resp: any) => {
            this._authService.accessToken = resp.token,
                subject.next({
                    list: resp.result
                })
        });
        return subject.asObservable()
    }

    calcularPrecioUnitario(payload: dtoCalcularUnitario) {
        return this.http.post<any>(`${environment.ventas.vta_venta_calcular_unitario}`, payload);
    }

    calcularItem(payload: dtoCalcularItem) {
        return this.http.post<any>(`${environment.ventas.vta_venta_calcular_item}`, payload);
    }
}