import { Component, EventEmitter, Input, OnDestroy, OnInit, Output, ViewChild } from '@angular/core';
import { Subject, Subscription, debounceTime, distinctUntilChanged, takeUntil } from 'rxjs';
import { MatTableDataSource } from '@angular/material/table'
import { FuseConfirmationService } from '@fuse/services/confirmation';
import { AppAlertService } from '@shared/service/app/alert.service';
import { FormControl } from '@angular/forms';
import { TablaDinamicaComponent } from '@shared/components/tabla-dinamica/tabla-dinamica.component';
import { MatDialog } from '@angular/material/dialog';
import { environment } from 'environments/environment';
import { SearchService } from 'app/layout/common/search/search.service';
import { MatButton } from '@angular/material/button';
import { IFilter } from '@shared/models/filter';
import { HeaderTextService } from '@shared/components/header-text/header-text.service';
import { Router } from '@angular/router';
import { EmpresaService } from '@shared/service/app/empresa.service';
import { PwaService } from '@shared/service/app/pwa.service';
import { VentaGenericasService } from '../service/venta-generica.service';
import { IVtaVentaSelect } from '../models/venta-generica';
import { FilterMenuComponent } from './filter-menu/filter-menu.component';
import { JedsMenuService } from '@shared/service/app/jeds-menu.service';

@Component({
    selector: 'app-venta-generica',
    templateUrl: './venta-genericas.component.html',
    styleUrls: ['./venta-genericas.component.scss']
})
export class VentaGenericasComponent implements OnInit, OnDestroy {
    // Necesarios para la app
    search: FormControl = new FormControl('');
    sortValue: string;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'border-primary text-primary';
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    // Particulares del modulo
    headers = ['', 'Origen', 'Numero', 'Fecha', 'Cliente', 'Vendedor', 'Total', 'Acciones'];
    column_params = ['seleccion', 'origen', 'numero_venta', 'fecha', 'cliente', 'vendedor', 'importe_total', 'acciones'];
    FilterEventSub$: Subscription;
    SearchTextSub$: Subscription;
    ventas: IVtaVentaSelect[];
    selectedItems: any[] = [];
    public dataSource: MatTableDataSource<IVtaVentaSelect> = new MatTableDataSource();
    funcionesObjeto = null;
    filtroBusqueda: string = '';
    filtersLike = ['cliente'];
    filter: IFilter = {
        vendedor: -1,
        cliente: -1,
        filtroFechaInicio: '',
        filtroFechaFin: ''
    };
    @Input() extraParams = '';
    @Output() parametrosActualizados = new EventEmitter<any>();
    env = environment.ventas.view_vta_venta_selects + '?pagination=true&per-page=10&sort=-fecha';

    constructor(
        private _ventaGenericasService: VentaGenericasService,
        private _empresaService: EmpresaService,
        private confirm: FuseConfirmationService,
        private alert: AppAlertService,
        private dialogo: MatDialog,
        private _searchService: SearchService,
        private router: Router,
        public _headerTextService: HeaderTextService,
        private pwaService: PwaService,
        private _menuService: JedsMenuService

    ) {
        this._headerTextService.setTitulo(this.router);

        /**
         * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
         */
        this.funcionesObjeto = [
            {
                nombre_boton: "editar",
                functionName: 'editar',
                iconoAccion: (venta: IVtaVentaSelect) => venta.puede_modificar === true ? 'jedstion:editar' : '',
                iconoAccionado: (venta: IVtaVentaSelect) => '',
                iconoDeshabilitado: (venta: IVtaVentaSelect) => venta.puede_modificar === false ? 'jedstion:editar_disabled' : '',
            },
            {
                nombre_boton: "eliminar",
                functionName: 'eliminar',
                iconoAccion: (venta: IVtaVentaSelect) => venta.puede_eliminar === true ? 'jedstion:eliminar' : '',
                iconoAccionado: (venta: IVtaVentaSelect) => '',
                iconoDeshabilitado: (venta: IVtaVentaSelect) => venta.puede_eliminar === false ? 'jedstion:eliminar_disabled' : '',
            },
            {
                nombre_boton: "Reporte",
                functionName: 'orden_servicio',
                iconoAccion: (venta: IVtaVentaSelect) => 'jedstion:imprimir',
                iconoAccionado: (venta: IVtaVentaSelect) => '',
                iconoDeshabilitado: (venta: IVtaVentaSelect) => '',
            }
        ];

        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });
    }

    @ViewChild(TablaDinamicaComponent) tabla: TablaDinamicaComponent;
    ngOnInit(): void {

        // Load empresa data
        this._empresaService.getEmpresa();
        this.SearchTextSub$ = this._searchService.getSearchText().pipe( //Se obtienen los valores que coinciden con lo ingresado en la barra de busqueda
            debounceTime(750),
            distinctUntilChanged()
        )
            .subscribe(value => {
                this.filtroBusqueda = value;
                this.tabla.filters(this.filtroBusqueda);
            });

        this.FilterEventSub$ = this._searchService.getFilterEvent().subscribe(event => {
            this.onFilterMenu(this._searchService.getFilterOrigin())
        });
    }

    saveVenta() {
        this.router.navigate(['/ventas/alta']);
    }

    updateVenta(event) {
        const id = event.params.data.idventa;
        this.router.navigate(['/ventas/edicion/', id]);
    }

    /**
    * Esta funcion elimina un item
    * @param event 
    */
    deleteVenta(event) {
        this.confirm.open({
            title: "Eliminar Venta: " + event.params.data.idventa,
            message: `¿Desea eliminar la Venta: ${event.params.data.idventa} -  ${event.params.data.cliente}?`,
            dismissible: false,
            actions: {
                confirm: {
                    show: true,
                    color: 'warn',
                    label: 'Si',
                },
                cancel: {
                    show: true,
                    label: 'No',
                },
            },
        })
            .afterClosed()
            .subscribe(res => {
                if (res === 'confirmed') {
                    this._ventaGenericasService.deleteVenta(event.params.data.idventa).subscribe({
                        next: (res) => {
                            // Manejar la respuesta exitosa
                            this.alert.success("Venta eliminada con éxito.");
                            if (res) {
                                this.tabla.renderTabla()
                            }
                        },
                        error: (error) => {
                            // Manejar el error
                            console.error('Error en el servicio:', error);
                            this.alert.warning("Esta Venta no puede eliminarse. Tiene elementos asociados.");
                        }
                    });
                }
            });
    }

    onFilterMenu(origin: MatButton) {
        this._menuService
            .open(FilterMenuComponent, origin._elementRef, {
                filter: this.filter,
                data: this.tabla.getElementosExistentes()
            })
            .afterClosed()
            .subscribe({
                next: filter => {
                    if (filter) {
                        this.filter = filter;
                        this.extraParams = this.filter.vendedor != -1 ? "filter[idusuario]=" + this.filter.vendedor['codigo'] + '&' : '';
                        this.extraParams += this.filter.cliente != -1 ? "filter[idcliente]=" + this.filter.cliente['id'] + '&' : '';
                        this.extraParams += this.filter.filtroFechaInicio != null ? "filter[fecha][gte]=" + this.filter.filtroFechaInicio + '&' : '';
                        this.extraParams += this.filter.filtroFechaFin != null ? "filter[fecha][lte]=" + this.filter.filtroFechaFin + '&' : '';
                        this.tabla.filters(this.filtroBusqueda);
                    }
                },
            });
    }

    /**
    * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
    * @param event 
    */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'editar':
                if (event.params.data.puede_modificar === false) {

                    if (event.params.data.tipo_venta === 1) {
                        return this.alert.warning('No se puede editar la venta.');
                    } else {
                        return this.alert.warning('No se puede editar una venta del sistema anterior.');
                    }

                } else {
                    this.updateVenta(event);
                }
                break;
            case 'eliminar':

                if (event.params.data.puede_eliminar === false) {
                    if (event.params.data.tipo_venta === 1) {
                        return this.alert.warning('No se puede eliminar la venta.');
                    } else {
                        return this.alert.warning('No se puede eliminar una venta del sistema anterior.');
                    }
                } else {
                    this.deleteVenta(event);
                }
                break;
            case 'orden_servicio':
                this.reporteOrdenServicio(event);
                break;
        }
    }

    /**
    * Esta función es la que se encarga de la seleccion de check box de Ventas seleccionados
    * @param selectedElements - La lista de Ordenes de servicio seleccionados.
    */
    onSelectionChange(selectedElements: any[]) {
        this.selectedItems = selectedElements;
    }

    /**
     * Esta funcion Genera un Reporte y lo muestra en una nueva pestaña
     */
    reporteOrdenServicio(event?) {
        let payload = null;

        // Si es atraves de presionar el boton de un item
        if (event) {
            // Selecciono el item
            this.selectedItems.push(event.params.data);
        }

        // Si no es atraves de presionar el boton del item Y no hay elementos en el array o la longitud del array es igual a cero
        if (!event && (!this.selectedItems || this.selectedItems.length === 0)) {
            return this.alert.error('Debe seleccionar al menos un Item.');
        } else {
            // Armo el payload para llamar al servicio
            payload = {
                // Creo un objeto con el valor del nombre de la clave y con el valor item con esa clave
                ordenes_de_servicio: this.selectedItems.map(element => {
                    let clave = element.tipo_venta === 1 ? 'idventa' : 'idventagenerica';
                    return { [clave]: element[`${clave}`] }
                })
            };

            this._ventaGenericasService.reporteOrdenServicio(payload).subscribe({
                next: (data) => {
                    if (data.success === true) {
                        const reportePath = data.path;

                        if (this.pwaService.isPwa()) {
                            this.router.navigate(['/reporte', reportePath]);
                        } else {
                            const reporteUrl = `${environment.baseRest}/${reportePath}`;
                            // Abre la pestaña aquí antes de la promesa
                            const newWindow = window.open('', '_blank');
                            if (newWindow) {
                                newWindow.location.href = reporteUrl; // Redirige la URL cuando tengas el reporte
                            } else {
                                console.error('No se pudo abrir la nueva pestaña.');
                            }
                        }

                        // Limpio la seleccion para que no se acumulen...
                        this.selectedItems = [];
                    } else {
                        console.error('Error al generar el reporte');
                    }
                },
                error: (error) => {
                    console.error('Error en la solicitud del reporte', error);
                }
            });

        }
    }

    ngOnDestroy(): void {
        this._searchService.unsubscribe();
        this.dialogo.closeAll();
    }
}